section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.players-container {
  display: flex;
  flex-wrap: wrap;
  margin: 2rem 0;
}

.player {
  padding: 1rem 2rem;
  margin: 3rem 2rem;
  border-radius: 3px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: all 0.2s;
  min-width: 10vw;
}

.player > h1 {
  margin: 1rem 0;
  font-weight: 100;
}

.player > h5 {
  font-weight: 100;
  font-size: 2.5rem;
}

.player.active {
  transform: scale(1.2);
}

.players-preview {
  padding: 2rem 3rem;
  width: 50vw;
  margin: 0 auto;
}

.transactions {
  list-style: none;
  padding: 0;
  margin-top: 1rem;
}

.transaction {
  font-size: 1.2rem;
  padding: 0.5rem;
  text-align: center;
  border-radius: 100px;
  margin: 0.5rem 0;
}

.transaction.plus {
  background-color: rgb(19, 201, 80);
  color: #fff;
}

.transaction.minus {
  background-color: rgb(236, 4, 113);
  color: #fff;
}

.denominations-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 1rem 0;
}

.denomination {
  padding: 1rem 2rem;
  width: 10rem;
  margin: 2rem;
  color: #fff;
  outline: none;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  font-size: 3.5rem;
}

.denomination--1 {
  background-color: #949494;
}

.denomination--2 {
  background-color: #ffc3c3;
}

.denomination--3 {
  background-color: #d0f4ff;
}

.denomination--4 {
  background-color: #b3c422;
}

.denomination--5 {
  background-color: #f3b6ff;
}

.amount {
  font-weight: 100;
  font-size: 10rem;
  user-select: none;
}

.amount.plus {
  color: rgb(19, 201, 80);
}

.amount.minus {
  color: rgb(236, 4, 113);
}

.confirm-btn {
  display: inline-block;
  padding: 1rem 2rem;
  outline: none;
  border: none;
  background: linear-gradient(to right, #C6FFDD, #FBD786, #f7797d);
  color: #fff;
  margin: 2rem 0;
  font-size: 2rem;
}
